<template>
	
	<section class="mylisten-details">

		<div class="full" v-show="show">
			<div class="box" >
				<ul>
					<li><p class="overflow"><span>课程名称 ：</span> {{ list.courseName}}</p></li>
					<li><p><span>参与人数 ：</span>{{list.count}}人</p></li>
					<li><p><span>上课时间 ：</span>{{list.toClassTime}}</p></li>
					<li><p><span>上课地点 ：</span>{{list.adress}}</p></li>
				</ul>

			</div>	
	         <p>{{$route.params}}</p>
			<div class="page-popup">
				<mt-popup
				  v-model="popupVisible"
				  position="top"
				  :modal=false
				  closeOnClickModal="false"
				  class="mint-popup-1">
		          <p>报名成功</p>
				</mt-popup>
			</div>
			
	   
	        <footer v-show="isShow">
	        	<div class="flex f-box">
	        		<a @click="cancel()">
	        		   取消试听
					</a>
	        	    <a @click="confrim">确认试听</a>
	        	</div>
	        </footer>

		</div>
		<div class="block" v-show="!show" style="text-align:center">
			<img src="../../assets/bg.png">
			   <p> 暂时没有排课信息</p>
		</div>	
	</section>
</template>

<script >
import MessageBox from  'mint-ui'
	 export default {
            data(){
            	return{
            	  popupVisible:false,
            	  show:true,
            	  isShow:true,
            	  orid:'',
            	  list:[],
				  appId:0,
				  oHttp:"http://117.34.105.176/AAEnglish/"
            	 }

            },
            mounted(){
            	this.appId =AndroidData.getAppId();
            	this.getData();                        	
            },
             watch: {
		      '$route': 'getData'
		    },
            methods:{  
                showFn(){
                     
                      if( this.$route.params.state.state==2){ //确认

                             this.isShow = false;    
                                            	     
                       }else{

                         	this.isShow = true;
                       };
                       if(this.$route.params.state.pkstate==2){ //未排课
                               this.show  = false
                       }else{
                       	
                         	this.show = true
                       }



                },
            	getData(){
		               var vm = this;		           
		               var orid = vm.$route.params.id;
		               this.showFn();
		               vm.$http.get(vm.oHttp+'ting/listeningDetils?rid='+vm.$route.params.state.rid)
		               .then((resp)=>{                    
		                     var res = resp.data;
		                     if(res.code==1){       

		                         console.log(res.data)
		                         vm.list =res.data;
		                     }                     
		               })
		               .catch((error)=>{
		                      console.log(error);
		               });
		             },
		        listenState(state,reson){ //shitingxiangqing
		          	      var vm = this;	                        
                          vm.$http(vm.oHttp+"ting/updateState?rid="+vm.$route.params.id+"&affirmState="+state+"&cancel="+reson)
							  .then((resp)=>{	
							  console.log(resp)							  					 
							  	if(state==3){ // 取消试听							  		
							  		 vm.$alert(resp.data.msg);							  		 
							  	  }else{
							  	  	 vm.popupVisible = true;	
							  	  }
							  	  window.history.go(-1); 
                                 setTimeout(function(){                 		  
			                 		     vm.popupVisible = false;

							  	         					                         
				                 },1000);
							  							  	
							  })
							  .catch((resp)=>{ // todo
							  });
		          },   
                 cancel() { // 取消试听

						this.$prompt('<textarea id="theReason" style="height:120px;width:100%;outline:mediumaquamarine;text-indent: 10px"onresize=false></textarea>', '请输入取消原因')
						.then(({message, value, action }) => {
						  var reson = 	document.getElementById("theReason").value
                               this.listenState(3,reson);

						},action =>{

							console.log("取消试听")
						})
						
						                 	
                 },
                 confrim(){ // 确认试听！
                 	// alert("确认试听！");
                 	var vm = this;
                 	vm.popupVisible = true;
                 	setTimeout(function(){                 		  
                 		  vm.listenState(1)
                         
                 	},2000);
                 }



            }
            
 

	 }
</script>
<style scoped>
	.block{
		height: 100%;
		width: 100%;
		text-align: center;
		
	}
	.block img{
		width: 200px;
		height: 200px;
		margin-top: 35%;
		padding: 5%
	}
#theReason{
	outline: medium;
	text-indent: 10px
}
	 .v-modal{

	      opacity: 0 !important;

	}

	   .v-modal{
	   	height: 5% !important;
	   } 
	   
	   .page-popup .mint-popup-1 {
		    width: 100%;
		    height: 50px;
		    text-align: center;
		    background-color: rgba(0,0,0,.7);
		    -webkit-backface-visibility: hidden;
		    backface-visibility: hidden;
		    z-index: 2000;
		}
	   .page-popup .mint-popup-1 p {
		    line-height: 50px;
		    color: #fff;
		}
	    .box{
	    	background-color: #fff;
	    	padding: 15px 20px;
	    	margin-bottom: 15px;
	    }

	    .overflow{
	        width: 100%;
	        overflow: hidden;
			text-overflow:ellipsis;
			white-space: nowrap;


	    }
	     .box p{
	        line-height: 32px;
	        color: #4AB7A2	;
	        font-size: 1rem
	    }
	    .box p span{
	    	color: #000;
	    }

	   .f-box a{
	   	    -webkit-box-flex: 1;
	        flex: 1;
	   }
	   .f-box a:first-child{

	   	    background-color: #fff;
	   	    color: #777
	   }
	    .f-box a{
	    	background-color: #FA9D33;
	    	color: #fff
	    }
   
</style>


