<template>
	
	<section class="mylisten-details">

		<div class="full" v-show="show">
			<div class="box"  >
				<ul>
					<li><p class="overflow"><span>课程名称 ：</span> {{ list.courseName}}</p></li>
					<li><p><span>参与人数 ：</span>{{list.count}}人</p></li>
					<li><p><span>上课时间 ：</span>{{list.goClassTime}}</p></li>
					<li><p><span>上课地点 ：</span>{{list.adress}}</p></li>
				</ul>

			</div>			
	         <!-- <p>{{$route.params}}</p> -->
			<div class="page-popup">
				<mt-popup
				  v-model="popupVisible"
				  position="top"
				  :modal=false
				  closeOnClickModal="false"
				  class="mint-popup-1">
		          <p>报名成功</p>
				</mt-popup>
			</div>
			

		</div>
		<div class="block" v-show="!show">
			<img src="../../assets/bg.png">
			<h3>暂时没有排课信息</h3>
		</div>	
	</section>
</template>

<script >
import MessageBox from  'mint-ui'
	 export default {
            data(){
            	return{
            	  popupVisible:false,
            	  show:true,
            	  isShow:true,
            	  orid:'',
            	  list:[],
				 appId:0,
				  oHttp:"http://117.34.105.176/AAEnglish/"
            	 }         
            },
            mounted(){
            	
            	this.appId = AndroidData.getAppId();
                this.getData();

            },
            methods:{  
            	getData(){
		               var vm = this;		           
		               var orid = vm.$route.params.id;
		               if(vm.$route.params.item.pkstate==2){ //未排课                         
	                        vm.show = false;
			            }else{
			            	vm.show = true;
			            }

		               vm.$http.get(vm.oHttp+'order/detiles?sid='+vm.$route.params.item.sid)
		               .then((resp)=>{                    
		                     var res = resp.data;
		                     if(res.code==1){                            
		                         console.log(res.data);
		                         vm.list =res.data;                          
		                     }                     
		               })
		               .catch((error)=>{
		                      console.log(error);
		               });
		             }

            }
            
 

	 }
</script>
<style scoped>
  html,body{
  	overflow: hidden;
  }

	.block{
		height: 100%;
		width: 100%;
		text-align: center;
		
	}
	.block img{
		width: 200px;
		height: 200px;
		margin-top: 35%;
		padding: 5%
	}
	   .v-modal{
	   	height: 5% !important;
	   } 
	   
	   .page-popup .mint-popup-1 {
		    width: 100%;
		    height: 50px;
		    text-align: center;
		    background-color: rgba(0,0,0,.7);
		    -webkit-backface-visibility: hidden;
		    backface-visibility: hidden;
		    z-index: 2000;
		}
	   .page-popup .mint-popup-1 p {
		    line-height: 50px;
		    color: #fff;
		}
	    .box{
	    	background-color: #fff;
	    	padding: 15px 20px;
	    	margin-bottom: 15px;
	    }

	    .overflow{
	        width: 100%;
	        overflow: hidden;
			text-overflow:ellipsis;
			white-space: nowrap;


	    }
	     .box p{
	        line-height: 32px;
	        color: #4AB7A2	;
	        font-size: 1rem
	    }
	    .box p span{
	    	color: #000;
	    }

	   .f-box a{
	   	    -webkit-box-flex: 1;
	        flex: 1;
	   }
	   .f-box a:first-child{

	   	    background-color: #fff;
	   	    color: #777
	   }
	    .f-box a{
	    	background-color: #FA9D33;
	    	color: #fff
	    }
   
</style>


