<template>
      <div id="detail-page">
      <div>
          	<div class="banner">
          		<img v-if="goodsInfo.cover" :src= oHttp+goodsInfo.cover />
                <img v-else src="../assets/bg-sm.jpg" alt="">
    	          <div class="context" >
    	          	<h3 class="title">{{goodsInfo.courseName}}</h3>
    	          	<p class="overflow3">{{goodsInfo.courseIntroduce}}</p>
    	          	<div class="tip"><span class="fl red">{{goodsInfo.coursePrice | formatMoney}}</span><span  class="fr orange shopping">限时抢购200天</span></div>
    	          </div>
          	</div>
          	<div class="box">
          	     <h3 class="title">课程介绍</h3>
          		<div v-if="detailList.length > 0" class="detailInfo" v-for="(item,index) in detailList" :key="index">
                    <img :src=oHttp+item.imgUrl></div>
                <div v-if="detailList.length <= 0" style="text-indent: 6px;font-size: 14px;line-height: 32px">暂无任何信息~</div>
          	</div>
      	</div>
         <mt-actionsheet :actions="actions" v-model="sheetVisible"></mt-actionsheet>
      	<footer>
      		<!--<div class="left fl"  @click="jump(goodsInfo.courseId)">已报名听</div>-->
            <div class="left fl"   @click="jump(goodsInfo.courseId)">报名试听</div>
      		<!--<div class="right fr" v-show="$route.params." @click="buy(list)">已购买</div>     -->
          <div class="right fr"   @click="buy(list)">购买</div>
      	</footer>
      </div>
</template>
<script >

	export default{
     data(){
        return{
            goodsInfo:{},
            sheetVisible:false,
            detailList:[],
            actions:[
                  {"name":"微信支付", method: this.weixinPay},
                  {"name":"支付宝支付",method:this.aliPay}],
            appId:0,
            oHttp:"http://192.168.8.22:8090/AAEnglish/"
        }
     },
     mounted(){
     //  this.appId = AndroidData.getAppId();
       this.getData();
     },
      watch: {
        '$route': 'getData'
      },
     methods:{
        getData(){
            var vm = this;
            vm.$http(vm.oHttp + "app/courseGoods/courseGoodsById.do?courseId="+vm.$route.params.id)
            .then((resp)=>{
                 vm.goodsInfo = resp.data.data;
            console.log(vm.goodsInfo.details !='');
                 if(vm.goodsInfo.details !=''){
                     vm.detailList = JSON.parse(vm.goodsInfo.details)
                 }else {
                     vm.detailList = [];
                 }
            }).catch((resp)=>{

            })
        },
        jump(id){
           var vm = this;
            vm.$alert("此功能暂不开放！");
//                    if(!vm.appId){
//                           vm.$alert("请登录！");
//                           return;
//                        }
//
//                   if(vm.$route.params.item.isListen){
//                      vm.$alert("你已经报名了！");
//                      return;
//                   }
//                   this.$router.push({ name: 'from', params: { id: id }})
        },
        buy(item){// 购买
               var vm = this;
               vm.$alert("此功能暂不开放！");
//                        if(vm.$route.params.hasBill){
//                          this.sheetVisible = true;
//                          return;
//                        }
//                        if(!vm.appId){
//                           vm.$alert("请登录！");
//                           return;
//                        }
//                        if(vm.$route.params.item.isBuying){
//                            vm.$alert("你已购买了该商品！");
//                            return;
//                         }
//                        this.$router.push({ name: 'buying', params: { id: item.bean.qid,total:item.bean.coursePrice }})
        } ,
        payAction(){
              this.sheetVisible = true;
           },
          weixinPay(){
              var vm = this;
              AndroidData.wxpay("vm.goodsInfo.courseName","22","vm.goodsInfo.coursePrice");

          },
          aliPay(){

            var timing = new Date().getTime();
            var vm = this;
            AndroidData.alipay("vm.goodsInfo.phone", timing, "www.baidu.com", "2","vm.goodsInfo.courseName" , "vm.goodsInfo.coursePrice")
          }

     }
	}

</script>
<style scoped>
	*{
		box-sizing: border-box;
	}
  .tip .shopping{
    display: inline-block;
  }
	html,body{
		font-size: 100px;

	}
	#detail-page{
		width: 100%;
		overflow: hidden;
	}
	#detail-page img{
		width: 100%;
	}
    #detail-page .detailInfo img{
        display: block;
        width: 100%;
    }
	.banner{
		height: auto;
		background-color: #fff
	}
	.banner img{
		height: 100%;
		width: 100%
	}
	.context{
		padding: 10px;
		font-size: 14px;
		font-weight: 100;
	}
	.title{
		font-size: 1rem;
		border-left: 3px solid #c33;
		text-indent: 0.4rem;

		margin:10px 0; 
	}
    .context p{
    	color: #777;
    	line-height: 24px;
    	font-size: 15px;
    }
    .tip{
    	line-height: 34px;
    	font-weight: 300;
    	height: 34px;
    }
    .box{
    	width: 100%;
    	overflow: hidden;
    	margin-top: 15px;
    	margin-bottom: 50px;
    	background-color: #fff
    }
    .box img{
    	width: 100%
    }
    .box .title{
    	margin: 10px 10px;
    }
     footer{
      position: fixed;
      bottom: 0;
      left: 0;
      right: 0;
     }
    footer .left{
    	width: 70%;
    	background-color: #fff;
    	color: #F4CD97
    }
    footer .right{
    	width: 30%;
    	background-color: #4AB7A2;


    }
</style>


