<template>	
	<section>
		<div class="page">
		 <div class="flex item-top">
		           <div class="location-icon">
		           	  <span class="location"></span>
		           </div>
		         	<ul class="item-top-ul">
		         		<li>		         		
		         			<p class="user-lcoation">{{pageList.adress}}</p>
		         		</li>
		         		<li>
		         			<p class="user-info">{{pageList.payName}}  &nbsp;&nbsp;&nbsp;  {{pageList.phone}}</p>
		         		</li>
		         	</ul>
		         	<p style="line-height:56px;" class="edit"></p>
		         </div>
			      <ul class="item">
			         <li >	        
			          <div class="item-box clearfix" @click="jump(pageList.qid,pageList.sid)">		            
			              <div class=" item-img-box" >
			                <img src="../../assets/book.png" class="item-img">
			              </div>
			              <div class=" item-context">  
			                 <h3>{{pageList.courseName}}</h3>
			                 <h5>
			                    <div>
			                    	参与人数 : <span>{{pageList.count}}人</span>
			                    </div>
		                       <div>
		                       	 <mt-button plain size="small" class="text-orange" >{{pageList.money | formatMoney }}</mt-button>
			                    
		                       </div>
			                 </h5>
			              </div>
			             </div>                    
			           </li>
			        </ul>
		</div>

        <mt-actionsheet
		  :actions="actions"
		  v-model="sheetVisible">
		</mt-actionsheet>
      <footer >
      	<div class=" flex flex-box">
	      		<a >		      		
		      		<span class="red">合计：{{pageList.money  | formatMoney }}</span>
	      	    </a>
	      	   <a @click="payAction()">去支付</a>
      	</div>
      </footer>
          
	</section>
</template>
<script>
	export default {
       data(){
       	    return{           	    	
       	    	 pageList:[],
       	    	  totalMoney:0,
       	    	  sheetVisible:false,
       	    	  actions:[
           	    	  {"name":"微信支付", method: this.weixinPay},
           	    	  {"name":"支付宝支付",method:this.aliPay}
       	    	  ],
       	    	  appId:0,
       	    	  oHttp:"http://117.34.105.176/AAEnglish/"
     
       	    }
           },
           mounted(){
               this.appId = AndroidData.getAppId();
               this.getData();                         
           },

           methods:{
           	goPay(){	           	    
	             	var vm  = this;
                    this.$router.push({ name: "buying", params: { id: vm.appId,total:vm.totalMoney}})
		     },      	        
           	jump(id,sid){
                 this.$router.push({ name: 'detail', params: { id: id ,hasBill:sid}})
            },
             getData(){
               var vm = this;
                var osid = vm.$route.params.id;
               vm.$http.get(vm.oHttp+'order/myOrder?sid='+osid)
               .then((resp)=>{                       
                     var res = resp.data;
                     if(res.code==1){                            
                          vm.pageList = res.data;
                        
                          vm.totalMoney = vm.pageList.money                    
                     }                     
               })
               .catch((error)=>{

                      console.log(error);
               });
             },
             payAction(){
                this.sheetVisible = true;
             },
            weixinPay(){                
                var vm = this;
                AndroidData.wxpay("vm.list.courseName","22","vm.list.coursePrice");

            },
            aliPay(){

              var timing = new Date().getTime();
              var vm = this;
              AndroidData.alipay("vm.list.phone", timing, "www.baidu.com", "2","vm.list.courseName" , "vm.list.coursePrice")
            }
             
            
           }
	}
</script>
<style scoped>
	 section{
	  	overflow: hidden !important;
	     height:auto 100%; 
	    width: 100%;
	    background-color: #F6F6F6;
		}
		.item{
		  position: relative;
		  background-color: #fff;
		    padding: 12px 8px;
		    margin:10px;
		}
        .item-top:after,
		.item:after{
		   content: '';
		   display: block;
		   width: 200%;
		   height: 1px;
		   position: absolute;
		   bottom: 0;
		   left: -50%;
		   background: #c0c0c0;
		   transform: scale(0.5);


		}
		.item-top{
			position: relative;
			padding:10px; 
		    padding-top: 0;
		    margin: 8px;
		    background-color: #fff;
		    line-height: 56px;
		}
		.item-top-ul{
			margin-top: 10px;
		}
		.location-icon{
			flex: none;
			width: 26px;
			background: url("../../assets/location-s.png") center center no-repeat;

		
		}
		
		.item-top-ul{
			flex: 1
		}
		.item-top p{			
			line-height: 22px;
			font-size: 14px;
		}
		.item-top .user-info{
			color: #444;
			line-height: 22px;
			font-size: 12px
		}

		.item-box{
		  overflow: hidden;
		  height: 100%;
		  padding:10px 0; 
		  display: flex;
		
		}
		
		.item-img-box{
		  flex: none;
		  width: 42px;
		  padding: 5px;

		}
		.item-img{
		  width: 100%
		}
		.item-context{
		   flex: 1;
		   padding: 0 10px;
		}
		.item-context h3{
		   font-size: 16px;
		   font-weight: 100;


		}
		.item-context h5{

			font-size: 14px;
			justify-content: space-between;
			align-content: space-between;
			display: flex;
			color: #777;
			line-height: 32px
		}

			
		
		.flex a{
			flex: 1;
			background: #fff;
			text-align: center;
		}
		.flex .item-check{
			width: auto;
			margin-left: 8px;
		}

		.flex a:last-child{
			flex: none;
			background: #49B6A1;
			width: 85px;
		}
		 .text-orange{
	      color: orange;
	       
	        border-color: orange;
	    }
	    .edit{
	    
	    	width: 24px;
	    	background: url("../../assets/edit.png") center center no-repeat;
	    	background-size: 12px;
	    }
</style>



