import Vue from 'vue';
import VueRouter from 'vue-router';
import Vuerify from 'vuerify';

import App from './App.vue';
import axios from 'axios';



import configRouter from './router.config.js';

import './common.js';
import 'mint-ui/lib/style.css';
import './mint-ui';

import MessageBox from '../node_modules/mint-ui/lib/message-box';


Vue.prototype.$confirm = MessageBox.confirm;
Vue.prototype.$alert = MessageBox.alert;


MessageBox.prompt = function(message, title, options) {
  if (typeof title === 'object') {
    options = title;
    title = '';
  }
  return MessageBox({
    title: title,
    message: message,
    showCancelButton: true,
    showInput: false,
    $type: 'prompt',
    inputType:'textarea'
  });
};

Vue.prototype.$prompt = MessageBox.prompt;


Vue.use(VueRouter)
Vue.use(Vuerify)
const router  = new VueRouter(configRouter);
//利用原型链把axios挂载到Vue上面


/*******common***********/
//filter
Vue.filter("formatMoney",function(value){
	console.log(value);
	var res = value==undefined?"￥ 00.00" :"￥" +((value*100)/100).toFixed(2)
	return   res  ;

});

Vue.filter("classState",function(value){
	return value==2?"未排课":"已排课";   
});

Vue.filter("confrimState",function(value){
	 return value ==1?"确认":value==2?"未确认":"取消";
});



Vue.prototype.$http =axios;

new Vue({
   router,
  el: '#app',
  render: h => h(App)
})



