<template>
    <div class="main-body" :style="{'-webkit-overflow-scrolling': scrollMode}">
        <v-loadmore :top-method="loadTop" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :auto-fill="false"
                    ref="loadmore">
            <ul>
                <li v-for="(item, key) in pageList" class="item"
                    @click="jump(item.courseId)">
                    <div class="item-box clearfix flex">
                        <div class="fl item-img-box">

                            <img v-if="item.cover" :src=oHttp+item.cover class="item-img">
                            <img v-else  src="../assets/cover.jpg" class="item-img">
                        </div>
                        <div class="fl item-context">
                            <h3>{{item.courseName}}</h3>
                            <h5>售价：<span>{{item.coursePrice | formatMoney }}</span></h5>
                            <div class="item-btns">
                                <!--<mt-button plain size="small"-->
                                           <!--@click.native.stop="clollect(item)" :class="{'current':item.isLike}"-->
                                           <!--class="collect">收 藏-->
                                <!--</mt-button>-->
                                <!--<mt-button plain size="small" v-show="item.isListen" @click.native.stop="listen(item)"-->
                                           <!--class="listen" :class="{'current':item.isListen}">-->
                                    <!--<span>&nbsp;已&nbsp;报&nbsp;名&nbsp;</span>-->
                                <!--</mt-button>-->
                                <!--<mt-button plain size="small" v-show="!item.isListen" @click.native.stop="listen(item)"-->
                                           <!--class="listen">-->
                                    <!--<span>试听报名</span>-->
                                <!--</mt-button>-->
                                <mt-button plain size="small" v-show="item.isBuying" @click.native.stop="buy(item)"
                                           class="buyCart">
                                    <span> 已购买</span>
                                </mt-button>
                                <mt-button plain size="small" v-show="!item.isBuying" @click.native.stop="buy(item)"
                                           :class="{'current':item.isBuy}" class="buyCart"><span>购 买</span>
                                </mt-button>
                            </div>
                        </div>
                    </div>
                </li>
            </ul>

        </v-loadmore>
    </div>
</template>
<script>
import {Loadmore} from 'mint-ui';
export default {
    data: function () {
        return {
            searchCondition: {  //分页属性
                page: 1,
                pageSize: 7
            },
            pageList: [],
            isLike: false,  
            totalGetCount: 0,
            allLoaded: false, //是否可以上拉属性，false可以上拉，true为禁止上拉，就是不让往上划加载数据了
            scrollMode: "auto", //移动端弹性滚动效果，touch为弹性滚动，auto是非弹性滚动
            appId: 0,
            oHttp:"http://192.168.8.22:8090/AAEnglish/"
        }
    },
    components: {
        'v-loadmore': Loadmore
    },
    mounted(){
         this.loadPageList();  //初次访问查询列表
    },
    watch: {
        '$route': 'loadPageList'
    },
    methods: {
        loadTop: function () { //组件提供的下拉触发方法
            //  下拉刷新
            var vm = this;
            setTimeout(function () {
                vm.loadPageList();
                vm.$refs.loadmore.onTopLoaded();// 固定方法，查询完要调用一次，用于重新定位
            }, 1000)
        },
        loadBottom: function () {
            // 上拉加载
            var vm = this;
            setTimeout(function () {
                vm.more(); // 上拉触发的分页查询
                vm.$refs.loadmore.onBottomLoaded();// 固定方法，查询完要调用一次，用于重新定位
            }, 1000)

        },
        loadPageList(){
            // 查询数据
            var vm = this;
            this.searchCondition.page = 1;
            this.searchCondition.pageSize = 7;
            vm.$http.get(vm.oHttp+"app/courseGoods/courseGoodsList.do?page=" + vm.searchCondition.page + "&pageSize=" + vm.searchCondition.pageSize)
                    .then(function (resp) {
                        vm.isHaveMore(resp.data.currentPage);
                        var List = [];
                        var res = resp.data.data.list;
                        res.forEach(function (item) {
                            item.isLike ? item.isLike=true : item.isLike=false;       
                             item.isListen ? item.isListen=true : item.isListen=false;                             
                             item.isBuying ? item.isBuying=true : item.isBuying=false;                    
                            List.push(item);
                        });
                        vm.pageList = List;
                        vm.totalGetCount = resp.data.totalPage;
                        vm.$nextTick(function () { //解决iphone端的弹性滚动效果
                            vm.scrollMode = "touch";
                        });

                    })
                    .catch(function (resp) {

                console.log(resp);

            })
        },
        more (){   // 分页查询
            var vm = this;
            this.searchCondition.page = parseInt(this.searchCondition.page) + 1;
            vm.$http.post(vm.oHttp+"app/courseGoods/courseGoodsList.do?&page=" + vm.searchCondition.page + "&pageSize=" + vm.searchCondition.pageSize)
                    .then(function (resp) {
                         var List = [];
                        resp.data.list.forEach(function (item) {
                            item.isLike ? item.isLike=true : item.isLike=false;  
                             item.isListen ? item.isListen=true : item.isListen=false;                             
                             item.isBuying ? item.isBuying=true : item.isBuying=false;   
                             List.push(item);
                        });
                        vm.pageList = vm.pageList.concat(List);
                        vm.isHaveMore(resp.data.data.currentPage);
                    })
                    .catch(function (resp) {
                console.log(resp);
            });
        },
        isHaveMore(isHaveMore){
            // 是否还有下一页，如果没有就禁止上拉刷新
            // true是禁止上拉加载
            if (isHaveMore == this.totalGetCount) {
                this.allLoaded = true;
            } else {
                this.allLoaded = false;
            }
        },
        jump(id){
            this.$router.push({name: 'detail', params: {id: id}});
        },
        clollect: function (item) {
            //收藏
            var vm = this;
            var state;
            if (!vm.appId) {
                vm.$alert("请先登录！");
                return;
            }
            if (typeof item.isLike == 'undefined') {
                vm.$set(item, "isLike", true);
            } else {
                item.isLike = !item.isLike;
            }
            if (item.isLike) {
                state = 1;
            } else {
                state = 2;
            }
            setTimeout(function () {
                vm.$http.post(vm.oHttp+"tcollect/insert?qid=" + item.qid + "&appId=" + vm.appId + "&state=" + state)
                        .then((resp) => {
                    if(resp.data.code==1){
                        vm.$alert(resp.data.msg);
                    }else{
                        alert(resp.data.msg);
                    }
            })
                .catch((resp) => {}
                )
            }, 500);
        },
        listen(item){
            //试听
            var vm = this;    
             if (vm.appId == 0) {
                vm.$alert("请登录！");
                return;
            }
             if(item.isListen){
                vm.$alert("你已经报名了！");
                return;
             }
            this.$router.push({name: 'from', params: {id: item.courseId}})
        },
        buy(item){
            var vm = this;
            vm.$alert("程序员正在拼命的开发~");
//            if (!vm.appId) {
//                vm.$alert("请登录！");
//                return;
//            }
//             if(item.isBuying){
//                vm.$alert("你已经购买了！");
//                return;
//             }
            console.log(item)
            // this.$router.push({name: 'buying', params: {id: item.courseId, total: item.coursePrice}})
            //购买
        }
    }
}
</script>

<style scoped>
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-weight: 100;
}

html, body {
    height: 100%;
    width: 100%;
}

dd, dl, li, ol, ul {
    margin: 0;
    padding: 0;
    list-style: none;
}
.item-btns{
  /*  display: inline-flex;*/
    flex: 1;
    justify-content: space-between;
    flex-flow: row no-wrap;
    display: flex; 
    vertical-align: bottom;

}
.item-btns .mint-button--small{

          margin-left: 4px;

}

.collect {
    background: url("../assets/heart.png") 4px center no-repeat;
}
.collect.current {
    background: url("../assets/heartgif.png") 4px center no-repeat;
}
.listen {
    background: url("../assets/listening.png") 4px center no-repeat;
}

.buyCart {
    background: url("../assets/cart.png") 4px center no-repeat;
}

.item {
    position: relative;
}
.active {
    background-color: #f00;
    color: #fff
}
.item:after {
    content: '';
    display: block;
    width: 200%;
    height: 1px;
    position: absolute;
    bottom: 0;
    left: -50%;
    background: #c0c0c0;
    transform: scale(0.5);

}

.item-box {
    /*  border-bottom: 1px solid #ccc;*/
    overflow: hidden;
    height: 100%;
    padding: 12px 8px;
    padding-right: 2px;
    height: 102px;
    flex: none;
}

.item-img-box {
    width: 85px;
    height: 100%;
    flex: none !important;

}
 .flex{
    justify-content: flex-start;
}

.item-img {
    height: 100%;
    width: 100%
}

.item-context {
    /* text-align: right;*/
/*    padding: 0 10px;
   box-sizing: border-box;*/
}

.item-context h3 {
    font-size: 16px;
    font-weight: 100;
    text-indent: 6px;

}
.item-context h5 {
    line-height: 34px;
    font-size: 15px;
       text-indent: 6px;
}

.item-context h5 span {
    color: #f00
}
.mint-button--small {
    padding: 2px 4px 2px 21px;
    height: 22px;
    text-align: right;
    position: relative;
    bottom: 0;
}
</style>


