    <template>

        <section>
            <div class="tab">
                <a v-for="(item,key) in tabHeader" :class="{active:item.isActive}" @click="tab(item.id,key)">{{item.name}}
            </a>
        </div>
        <div class="page-tab-container">
            <mt-tab-container v-model="active" value="tab-container1" v-show="ifShow">
            <mt-tab-container-item id="tab-container1">
            <ul>
            <li v-for="(item, key) in list" class="item"
            @click="jump('orderDetail',item.sid)" >
            <div class="item-box clearfix">
                <div class="item-check">
                    <span class="checkbox" :class="{'active':item.checked}" @click.stop="select(item)">
                </span>
            </div>
            <div class=" item-img-box">
                <img src="../../assets/book.png" class="item-img">
            </div>
            <div class=" item-context">
                <h3>{{item.courseName}}</h3>
                <h5>
                    <div>
                        参与人数 : <span>{{item.count}}人</span>
                    </div>
                    <div>
                        <mt-button plain size="small" class="text-orange">{{item.money | formatMoney}}</mt-button>

                    </div>
                </h5>
            </div>
        </div>
    </li>
  </ul>
    <div class="block" v-show="!ifShow">
    <img src="../../assets/bg.png">
    <h3>暂时没有排课信息</h3>
    </div>
            </mt-tab-container-item>
            <mt-tab-container-item id="tab-container2" v-show="ifShow2">
    <ul>
        <li v-for="(item, key) in paiedList" class="item"
        @click.stop="jump('billDetail',item.sid,item)">
        <div class="item-box clearfix">
            <div class=" item-img-box">
                <img src="../../assets/book.png" class="item-img">
            </div>
            <div class=" item-context">
                <h3>{{item.courseName}}</h3>
                <h5>
                    <div>
                        参与人数 : <span>{{item.count}}人</span>
                    </div>
                    <div>
                        <mt-button plain size="small" class="text-orange status">{{ item.pkstate | classState }}</mt-button>
                        <mt-button plain size="small" class="text-orange">{{item.money | formatMoney}}</mt-button>
                    </div>
                </h5>
            </div>
        </div>
    </li>
    </ul>
    <div class="block" v-show="!ifShow2">
    <img src="../../assets/bg.png">
    <h3>暂时没有排课信息</h3>
    </div>
            </mt-tab-container-item>
            </mt-tab-container>
            </div>
    <mt-actionsheet
    :actions="actions"
    v-model="sheetVisible">
    </mt-actionsheet>


    <footer v-show="isShow">
    <div class=" flex flex-box">
        <a>
            <span class="item-check fl"><i style="margin-top:2px；" class="checkbox " :class="{'active':checkAllFlag}" @click="selectAll(true)"></i>
            <span style="margin-top:-10px; margin-top: -9px;vertical-align: middle;display: inline-block;">全选</span></span>
        <span class="red">合计：{{ totalMoney |formatMoney}} </span>
    </a>
    <a @click="payAction()">去支付
    </a>
            </div>
            </footer>

            </section>
            </template>
    <script>
    export default {

        data(){
            return {
                active: "tab-container1",
                swipeable: false,
                isShow: true,
                oHttp:"http://117.34.105.176/AAEnglish/",
                tabHeader: [{
                    id: "tab-container1",
                    name: "代付款",
                    isActive: true
                }, {
                    id: "tab-container2",
                    name: "已购买",
                    isActive: false
                }],
                allData: {
                    parCheck: false,
                    text: '全选'
                },
                paiedList: [],
                list: [],
                totalMoney: 0,
                checkAllFlag: false,
                count: 1,
                appId: 0,
                ifShow: true,
                ifShow2: true,
                sheetVisible: false,
                actions: [
                    {"name": "微信支付", method: this.weixinPay},
                    {"name": "支付宝支付", method: this.aliPay}
                ]
            }
        },
        mounted(){
            this.appId = AndroidData.getAppId();
            this.$nextTick(function () {
                this.getData();
            });
         
        },
        methods: {
            payAction(){
                this.sheetVisible = true;
            },
            weixinPay(){
                var vm = this;
                AndroidData.wxpay("新概念英语", "22", "55");
            },
            aliPay(){
                var timing = new Date().getTime();
                var vm = this;
                AndroidData.alipay("18700974814", timing, "www.baidu.com", "2", "新概念英语", "55");

            },
            goPay(){
                if (!vm.appId) {
                    vm.$alert("请先登录！");
                    return;
                }
                var vm = this;
                this.$router.push({name: "buying", params: {id: vm.appId, total: vm.totalMoney}})
            },
            jump(path, id, item){

                this.$router.push({name: path, params: {id: id, item: item}})
            },
            selectAll: function (flag) { // 全选

                var vm = this;
                if (this.count % 2) {
                    this.checkAllFlag = true;
                    this.count++;
                } else {
                    this.checkAllFlag = false;
                    this.count--;

                }
                this.list.forEach(function (item, index) {
                    if (typeof item.checked == 'undefined') {//也要防止未定义
                        vm.$set(item, "checked", vm.checkAllFlag);// 通过set来给item添加属性checked
                    } else {
                        item.checked = vm.checkAllFlag;
                    }
                });
                this.calcTotalPrice();

            },
            select(item){   //单选
                var vm = this;
                if (typeof item.checked == 'undefined') {
                    vm.$set(item, "checked", true);

                } else {
                    item.checked = !item.checked;
                }
                vm.isCheckAll();
                vm.calcTotalPrice();

            },
            calcTotalPrice(){  //计算总价
                var vm = this;
                vm.totalMoney = 0;

                vm.list.forEach(function (item, index) {                 
                    if (item.checked) {                         
                        vm.totalMoney += item.money*1;
                    }
                });


            },
            isCheckAll() {  //判断是否选择
                var vm = this;
                this.checkAllFlag = true;
                this.list.forEach(function (item, index) {
                    if (item.checked !== true) {
                        vm.checkAllFlag = false;
                    }
                });
            },
            getData(){   //获取数据
                var vm = this;
                vm.$http.get(vm.oHttp+"order/myNoPay?appid="+vm.appId)
                        .then((resp) => {
                    var res = resp.data;
                if (res.code == 1) {
                    if (res.data.length == 0) {
                        vm.ifShow = false;
                    } else {
                        vm.ifShow = true;
                        vm.list = res.data;
                        console.log(vm.list)
                    }
                }

            })
            .catch((error) => {
                    console.log(error);
            });
            vm.$http.get(vm.oHttp+"order/myYetPay?appid="+vm.appId)
                .then((resp) => {
                    var res = resp.data;

                if (res.data.length == 0) {
                    vm.ifShow = false;
                } else {
                    vm.ifShow = true;
                    vm.paiedList = res.data;
                }
            })
            .catch((error) => {

                    console.log(error);
            });


            },
            tab(id, index){  //选项切换
                if (index % 2) {
                    this.isShow = false;
                } else {
                    this.isShow = true;
                }
                this.active = id;
                this.tabHeader.forEach(function (item) {
                    item.isActive = false;
                });

                this.tabHeader[index].isActive = true;

            }
        }
    }
    </script>
    <style scoped>

    .block {
        height: 100%;
        width: 100%;
        text-align: center;
        color: #333
    }

    .block img {
        width: 200px;
        height: 200px;
        margin-top: 35%;
        padding: 5%
    }

    .checkbox {
        display: inline-block;
        height: 20px;
        width: 20px;
        margin-top: 14px;
        background: url("../../assets/check.png") left center no-repeat;
        background-size: contain;
    }

    .checkbox.active {
        background: url("../../assets/checked.png") left center no-repeat;
        background-size: contain;
    }

    .tab {

        display: flex;
        justify-content: space-around;
        height: 50px;
        line-height: 50px;
        background-color: #fff;
        text-align: center;
        border-bottom: 1px solid #ccc;
    }

    .tab a {
        flex: 1;
    }

    .tab a.active {
        color: #4AB7A2;
        /*border-bottom: 2px solid #4AB7A2;*/
        background: url("../../assets/border.png") bottom left no-repeat;
        background-size: contain;
        margin-bottom: -1px;
    }

    .item {
        position: relative;
    }

    .item:after {
        content: '';
        display: block;
        width: 200%;
        height: 1px;
        position: absolute;
        bottom: 0;
        left: -50%;
        background: #c0c0c0;
        transform: scale(0.5);

    }

    .item-box {
        /*  border-bottom: 1px solid #ccc;*/
        overflow: hidden;
        height: 100%;
        padding: 12px 8px;
        display: flex;
        background-color: #fff
    }

    .item-check {
        width: 22px;
        color: #000;
    }

    .item-img-box {
        flex: none;
        width: 42px;
        padding: 5px;

    }

    .item-img {
        width: 100%
    }

    .item-context {
        flex: 1;
        padding: 0 10px;
    }

    .item-context h3 {
        font-size: 16px;
        font-weight: 100;

    }

    .item-context h5 {

        font-size: 14px;
        justify-content: space-between;
        align-content: space-between;
        display: flex;
        color: #777;
        line-height: 32px
    }

    .mint-button--small {
        padding: 0 6px;
        height: 22px;
        font-size: 12px;
        text-align: right;
    }

    .text-orange {
        color: orange;

        border-color: orange;
    }

    .text-orange.status {
        color: #4AB7A2;
        border-color: #ccc
    }

    .flex a {
        flex: 1;
        background: #fff;
        text-align: center;
    }

    .flex .item-check {
        width: auto;
        margin-left: 8px;
    }

    .flex a:last-child {
        flex: none;
        background: #49B6A1;
        width: 85px;
    }
    </style>



