<template>
	<div class="listen">
		
      <div class="wrapper">
         <mt-field label="用户名 :" placeholder="请输入用户名" :value.sync="username"  v-model="username" :state="errors.username ? 'error': username ? 'success': ''"></mt-field>
        
         <mt-field label="电  话 :" placeholder="请输入电话" type="number"  v-model="tel" :state="errors.tel ? 'error': tel ? 'success': ''"></mt-field>

         <div class="box">
             <div class="box-before"></div>
             <div class="box-controll">
             	 <label class="box-label">选择省:</label>
             	 <div class="box-input">
             	 		<select class="box-field-core " id="province" v-model="selectedProvince" name="proDId">
             	 		    <option>--请选择--</option>
			          		<option  v-for="(item,index) in listData" :value="index" :id="item.prid">{{item.provincename}}</option>			          		
			          	</select>
             	 </div>	          
             </div>           
          </div>

          <div class="box">
             <div class="box-before"></div>
             <div class="box-controll">
             	 <label class="box-label">选择市:</label>
             	 <div class="box-input">
             	 		<select class="box-field-core " id="city" v-model="selectedCity" name="cityDId">
             	 		    <option selected>--请选择--</option>
			          		<option :value="index" :id="item.cityid"  v-for="(item, index) in cities">{{item.cityname}}</option>			          	
			          	</select>
             	 </div>
	          
             </div>           
          </div>
          <div class="box">
             <div class="box-before"></div>
             <div class="box-controll">
             	 <label class="box-label">选择区:</label>
             	 <div class="box-input">
             	 		<select class="box-field-core area" id="area"  v-model="selectedBlock" name="regionDId">
             	 		    <option>--请选择--</option>
			          		<option :value="index" :id="item.dId" v-for="(item, index) in blocks">{{item.dName}}</option>			          		
			          	</select>
             	 </div>
	          
             </div>           
          </div>
           <mt-field label="详细地址" placeholder="请输入详细地址"  v-model="address" :state="errors.address ? 'error': address ? 'success': ''" ></mt-field>  
      </div>
     <!--  <ul><li v-for="err in errors" v-text="err"></li></ul> -->
    <footer>
    	<mt-button :disabled="invalid"  @click.native="confirmSign()">确认试听</mt-button>
    </footer>
	</div>
</template>

<script>
import Vue from 'Vue';
		export default {
			data(){
				return{
					username:'',
					tel:'',			
					address:'',
				    selectedProvince: '',
			        selectedCity: '',
			        selectedBlock:'',
			        cities: 0,		
			        blocks: 0,
			        listData:[],
			        appId:0,
			        proDId:'',
			        cityDId:'',
			        regionDId:'',
			        sheetVisible:false,
			        popupVisible:false,
			         oHttp:"http://117.34.105.176/AAEnglish/",
			         actions:[
	           	    	  {"name":"微信支付", method: this.weixinPay},
	           	    	  {"name":"支付宝支付",method:this.aliPay}
           	    	  ],
           	    	  list:[]


				}
			},
		   
			watch:{            
                selectedProvince(newVal,oldVal){
                       if(newVal==undefined){
                         	return;
                       }
                      this.cities = this.listData[newVal].cabeans;   

                      this.proDId = this.listData[newVal].prid;               
                      var _this = this;
				      Vue.nextTick(() => {

				        _this.selectedCity = _this.cities[0];				       
				        _this.$emit('input', _this.info)
				        
				      })
                },
                selectedCity(newVal,oldVal){                                                          
                 
                        var _this = this;                           
                      	var area = this.listData[_this.selectedProvince].cabeans[newVal];
    
                      	

                      	if (area) {
                      		  this.blocks = area.areaAtbs;
                      		  this.cityDId = area.cityid;
                      	}else{
                      		this.blocks = [];
                      	}
                  	      Vue.nextTick(() => {
						        _this.selectedBlock = _this.blocks[0];				      
						        _this.$emit('input', _this.info);

				         })
                       

                },
                selectedBlock(){
                      var _this = this
				      Vue.nextTick(() => {
				        _this.$emit('input', _this.info)
				      });
                }
			},
			mounted(){
				this.getJson();
				this.appId = AndroidData.getAppId();

			},
			computed: {
			    errors () {
			      return this.$vuerify.$errors
			    },
			    invalid () {

			      return this.$vuerify.invalid
			  
			    },
			     info(){                          
                    return {
				        proDId: this.proDId,
				        cityDId: this.cityDId,
                        regionDId: this.regionDId,
                        name:this.username	,
                        phone:this.tel,
                        adress:this.adress,
                        orderSource:"APP",
                         appId:this.appId,
                        qid:this.$route.params.id

			      }
               }
			},
			methods:{
				confirmSign(){                     
					var vm = this;    
	                vm.$http.post(vm.oHttp+"ting/sign",vm.info)
	                   .then((resp)=>{      

	                          if (resp.data.code==1) {
                                  vm.$alert("报名成功") ;
	                          	  vm.$router.go(-1);
	                          }else{
	                          	 vm.$alert("操作失败") ;
	                          }	                          	                           
	                   })
	                   .catch(resp=>{

	                   });
               },
              
               getJson(){

                   var vm = this;
                   vm.$http(vm.oHttp+"d/procityregion")
                   .then(resp=>{     
                              console.log(resp)
                            vm.listData = resp.data.list;
                           
                   })
                   .catch(resp=>{


                   });

               }
			},
			 vuerify: {
			    username: {
			      test: /\S{1,}/,  
			      message: '至少 1 位字符'
			    },
			    tel: { 
				    test:/^1(3|4|5|7|8)\d{9}$/,
				    message: '请输入正确的电话号码'
				}				
			  }
		}

</script>
<style scoped >
	.wrapper{
		font-size: 14px;
	}
     footer{
     
     	color: #fff;
     	cursor: pointer;
     }
     footer .mint-button {
     	display: block;
     	width: 100%;
     	height: 100%;
     	border-radius: 0;
     	background-color: #FA9D33;
     	color: #fff
     }
     .box{
	    background-color: #fff;
	    box-sizing: border-box;
	    color: inherit;
	    min-height: 48px;
	    display: block;
	    overflow: hidden;
	    position: relative;
	    text-decoration: none;


     }
     .box-before{
 	    position: absolute;
	    height: 100%;
	    left: 0;
	    -webkit-transform: translate3d(-100%, 0, 0);
	    transform: translate3d(-100%, 0, 0);
     }
       .box-controll:first-child{

             background-image: none;
	    background-image: none;

     }
    .box-controll{

	    background-image: -webkit-linear-gradient(top, #d9d9d9, #d9d9d9 50%, transparent 50%);
	    background-image: linear-gradient(180deg, #d9d9d9, #d9d9d9 50%, transparent 50%);
	    background-size: 120% 1px;
	    background-repeat: no-repeat;
	    background-position: top left;
	    background-origin: content-box;
	    -webkit-box-align: center;
	    -ms-flex-align: center;
	    align-items: center;
	    box-sizing: border-box;
	    display: -webkit-box;
	    display: -ms-flexbox;
	    display: flex;
	    font-size: 16px;
	    line-height: 1;
	    min-height: inherit;
	    overflow: hidden;
	    padding: 0 10px;
	    width: 100%;
    }
    .box-input{
    	    -webkit-box-flex: 1;
		    -ms-flex: 1;
		    flex: 1;
		    color: inherit;
		    display: -webkit-box;
		    display: -ms-flexbox;
		    display: flex;
		 
    }
    
    .box-field-core{
	    appearance: none;
	    border-radius: 0;
	  /*  border: 0;*/
	    -webkit-box-flex: 1;
	    -ms-flex: 1;
	    flex: 1;
	    outline: 0;
	    line-height: 1.6;
	    font-size: inherit;
	    width: 100%;
        font-size: 14px;
        padding: 5px 0;

    }
    .box-label{
    	width: 95px;
    	-webkit-box-flex: 0;
	    -ms-flex: none;
	    flex: none;
	    

    }

</style>


