<template>
	<div class="my-listen">		
	       <ul>
	         <li  v-for="(item, key) in pageList" class="item"  @click="jump(item.rid,item)">
	          <div class="item-box clearfix">
	              <div class="fl item-img-box" >
	                <img src="../../assets/book.png" class="item-img">
	              </div>
	              <div class="fl item-context">  
	                 <h3>{{item.courseName}}</h3>
	                 <h5>
	                    <div>
	                    	参与人数 : <span>{{item.count}}人</span>
	                    </div>
                       <div>
                       	 <mt-button plain size="small" class="text-orange"  @click.native.stop="clollect(key)">{{item.pkstate | classState}}</mt-button>
	                       <mt-button plain size="small" class="text-orange status" @click.native.stop="listen(item)">{{item.affirmState | confrimState}}</mt-button>
                       </div>
	                 </h5>
	              </div>
	             </div>                    
	           </li>

        </ul>
	</div>

</template>
<script >
export default {
         data(){
             return{
                 pageList:[],
                 appId:0,
                 oHttp:"http://117.34.105.176/AAEnglish/"
             }
         },
          watch: {
              '$route': 'getData'
          },
          mounted(){

              this.appId = AndroidData.getAppId();
              this.getData();            
          },
          methods:{
              jump(id,state){
                var vm = this; 
    
                   this.$router.push({ name: 'listenDetails', params: { id: id,state:state }});

          },
           getData(){
               var vm = this;              
               vm.$http.get(vm.oHttp+'ting/listeningList?appid='+vm.appId)
               .then((resp)=>{                       
                     var res = resp.data;
                     if(res.code==1){                            
                          vm.pageList = res.data;
                     }                     
               })
               .catch((error)=>{

                      console.log(error);
               });
             }

         }

}



</script>
<style scoped>
      	.item{
        position: relative;
      }
      .active{
        background-color: #f00;
        color: #fff
      }
      .item:after{
         content: '';
         display: block;
         width: 200%;
         height: 1px;
         position: absolute;
         bottom: 0;
         left: -50%;
         background: #c0c0c0;
         transform: scale(0.5);


      }
      .item-box{
      /*  border-bottom: 1px solid #ccc;*/
        overflow: hidden;
        height: 100%;
        padding: 12px;
        display: flex;
        background-color: #fff
      }
      .item-img-box{
        flex: none;
        width: 42px;
        padding: 5px;

      }
      .item-img{
        width: 100%
      }
      .item-context{
         flex: 1;
         padding: 0 10px;
      }
      .item-context h3{
         font-size: 16px;
         font-weight: 100;


      }
      .item-context h5{

      	font-size: 13px;
      	justify-content: space-between;
      	align-content: space-between;
      	display: flex;
      	color: #777;
      	line-height: 32px
      }

      .mint-button--small{
      	padding: 0 6px;
        width: 56px;
      	height: 22px;
      	 font-size: 13px;
          text-align: center;
      }
      .text-orange{
      	color: orange;
         
          border-color: orange;
      }
      .text-orange.status{
          color: #4AB7A2;   
          border-color: #ccc


      }

</style>


