import Home from './component/Home.vue';
import Detail from './component/detailPage.vue'


import Buying from './component/buying.vue'

import Listen from './component/listen.vue'

import MyListen from './component/myListen/mylisten.vue'
import MyListenDtails from './component/myListen/myListenDetals.vue'


import Order from './component/order/orderList.vue'
import BillDetail from './component/order/orderDetails.vue'
import MyOrder from './component/myorder/myOrderList.vue'

export default {
    routes: [
        {path: '/from/:id', name: 'from', component: Listen},
        {path: '/buying/:id', name: 'buying', component: Buying},
        {path: '/detail/:id', name: 'detail', component: Detail},
        {path: '/order/:id', name: 'orderDetail', component: MyOrder},
        {path: '/OrderDetail/:id', name: 'billDetail', component: BillDetail},
        {path: '/listenDetails/:id', name: 'listenDetails', component: MyListenDtails},
        {path: '*', component: Home}
    ]
}



